/* View Controls */

.views {
  position:fixed;
  right: 10px;

  display:flex;
  flex-direction: column;
}

.views button {
  height:70px;
  width:70px;
  z-index:17;
  margin:5px;
}

/* Timeline */

.timeline {
  position:fixed;
  top:50%;
  left:5%;

  width:90%;
  height:3px;
  background-color:black;
}

/* Height is double what is removed from `top` */
.timeline-tick {
  position:absolute;
  top:calc(50% - 15px);
  left:0%;

  height:30px;
  width:2px;
  background-color:black;
}

.timeline-month {
  margin-left:5px;
  margin-top:15px;
  font-weight: 700;
}

/* Images */

.images-container {
  display:flex;
  flex-direction: row;
  flex-wrap: wrap;
  text-align: center;
  position:absolute;
}

.image-module {
  display:flex;
  flex-direction: row;
  /* position: relative; */

  widows: 230px;
  height: 230px;
  margin-right:1%;
  margin-bottom:1%;
}

@media only screen and (max-width: 768px) {
  .image-module {
    width:80%;
  }
}

.image-module img {
  /* max-width:100%; */
  width:200px;
  height:200px;
  /* transition: all .3s ease-in-out; */
}

.image-palette {
  /* position:absolute; */
  /* right:0px; */
  width:30px;
  display: flex;
  flex-direction: column;
}

.image-palette > * {
  transition: all .3s ease-in-out;
}